@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&family=Open+Sans:wght@400;700&display=swap";
:root {
  --primary: #4a90e2;
  --primary-light: #82b1ff;
  --secondary: #fff;
  --accent: #ff6f61;
  --accent-dark: #ff3b2d;
  --bg-gradient: linear-gradient(135deg, #e0eafc, #cfdef3);
  --card-bg: #fffc;
  --blur-light: 5px;
  --blur-medium: 8px;
  --shadow: #0000001a;
  --border-radius: 12px;
  --transition: .3s;
  --transition-fast: .2s;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: var(--bg-gradient);
  color: #333;
  min-height: 100vh;
  padding: 20px;
  font-family: Roboto, sans-serif;
  line-height: 1.6;
}

header {
  text-align: center;
  -webkit-backdrop-filter: blur(10px);
  border-radius: var(--border-radius);
  box-shadow: 0 4px 12px var(--shadow);
  background: #fff9;
  border-bottom: 2px solid #ffffff4d;
  margin-bottom: 30px;
  padding: 20px;
  font-family: Open Sans, sans-serif;
  font-size: 2rem;
}

nav {
  color: var(--secondary);
  text-align: center;
  border-radius: var(--border-radius);
  box-shadow: 0 4px 12px var(--shadow);
  background: #4a90e2e6;
  margin-bottom: 30px;
  padding: 15px;
  font-size: 1.1rem;
}

.card {
  background: var(--card-bg);
  -webkit-backdrop-filter: blur(var(--blur-medium));
  border-radius: var(--border-radius);
  box-shadow: 0 8px 25px var(--shadow);
  transition: transform var(--transition), box-shadow var(--transition);
}

.card:hover {
  box-shadow: 0 12px 30px var(--shadow);
  transform: translateY(-5px);
}

.sign-in-main {
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 200px);
  display: flex;
}

.sign-in-container {
  text-align: center;
  background: var(--card-bg);
  width: 400px;
  -webkit-backdrop-filter: blur(var(--blur-light));
  border-radius: var(--border-radius);
  box-shadow: 0 10px 25px var(--shadow);
  transition: transform var(--transition);
  padding: 40px;
}

.sign-in-container:hover {
  transform: translateY(-5px);
}

.sign-in-container h2 {
  color: var(--primary);
  margin-bottom: 20px;
  font-size: 2.5rem;
}

.sign-in-container p {
  color: #666;
  margin-bottom: 30px;
  font-size: 1rem;
}

#googleSignInBtn {
  background: var(--primary);
  color: var(--secondary);
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: background var(--transition), transform var(--transition);
  border: none;
  padding: 15px 30px;
  font-size: 1rem;
}

#googleSignInBtn:hover {
  background: var(--primary-light);
  transform: translateY(-3px);
}

#book-form-section {
  background: var(--card-bg);
  max-width: 600px;
  -webkit-backdrop-filter: blur(var(--blur-medium));
  border-radius: var(--border-radius);
  box-shadow: 0 8px 25px var(--shadow);
  transition: box-shadow var(--transition);
  margin: 40px auto;
  padding: 40px;
}

#book-form-section:hover {
  box-shadow: 0 12px 30px var(--shadow);
}

#book-form-section h2 {
  color: var(--primary);
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
}

form input, form select {
  width: 100%;
  transition: border var(--transition), box-shadow var(--transition);
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 15px;
  font-size: 1rem;
}

form input:focus, form select:focus {
  border-color: var(--primary);
  box-shadow: 0 0 8px var(--primary);
  outline: none;
}

form button {
  background: linear-gradient(45deg, var(--primary), var(--primary-light));
  color: var(--secondary);
  cursor: pointer;
  transition: transform var(--transition-fast), box-shadow var(--transition-fast);
  border: none;
  border-radius: 30px;
  padding: 15px 30px;
  font-size: 1rem;
}

form button:hover {
  box-shadow: 0 4px 12px var(--shadow);
  transform: translateY(-3px);
}

#book-list-section {
  background: var(--card-bg);
  max-width: 800px;
  -webkit-backdrop-filter: blur(var(--blur-medium));
  border-radius: var(--border-radius);
  box-shadow: 0 8px 25px var(--shadow);
  transition: box-shadow var(--transition);
  margin: 40px auto;
  padding: 30px;
}

#book-list-section:hover {
  box-shadow: 0 12px 30px var(--shadow);
}

#book-list-section h2 {
  color: var(--primary);
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
}

ul#bookList {
  padding: 0;
  list-style: none;
}

ul#bookList li {
  -webkit-backdrop-filter: blur(3px);
  transition: background var(--transition), transform .2s;
  background: #ecf0f1cc;
  border-radius: 8px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding: 20px;
  display: flex;
}

ul#bookList li:hover {
  background: #dcdde1cc;
  transform: translateX(5px);
}

ul#bookList li span {
  flex: 1;
  font-family: Open Sans, sans-serif;
}

.edit-btn, .delete-btn {
  cursor: pointer;
  color: var(--secondary);
  border: none;
  border-radius: 20px;
  margin: 5px;
  padding: 10px 20px;
  font-size: .9rem;
  transition: transform .2s, box-shadow .2s;
}

.edit-btn {
  background: linear-gradient(45deg, #00c6ff, #0072ff);
}

.edit-btn:hover {
  box-shadow: 0 3px 10px var(--shadow);
  transform: translateY(-2px);
}

.delete-btn {
  background: linear-gradient(45deg, var(--accent), var(--accent-dark));
}

.delete-btn:hover {
  box-shadow: 0 3px 10px var(--shadow);
  transform: translateY(-2px);
}

.sidebar {
  background: var(--card-bg);
  width: 280px;
  -webkit-backdrop-filter: blur(var(--blur-medium));
  border-radius: var(--border-radius);
  box-shadow: 0 8px 25px var(--shadow);
  transition: transform var(--transition);
  margin: 40px auto;
  padding: 30px;
}

.sidebar:hover {
  transform: translateX(3px);
}

.sidebar h2 {
  color: var(--primary);
  text-align: center;
  margin-bottom: 20px;
  font-size: 1.8rem;
}

.sidebar select {
  background: var(--secondary);
  width: 100%;
  transition: border var(--transition), box-shadow var(--transition);
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 15px;
  font-size: 1rem;
}

.sidebar select:focus {
  border-color: var(--primary);
  box-shadow: 0 0 8px var(--primary);
  outline: none;
}

.container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  max-width: 1200px;
  margin: auto;
  padding: 20px;
  display: flex;
}

.main-content {
  flex: 1;
  min-width: 300px;
}

#chat-widget {
  z-index: 1000;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

#chat-circle {
  background: var(--primary);
  width: 70px;
  height: 70px;
  color: var(--secondary);
  cursor: pointer;
  box-shadow: 0 10px 25px var(--shadow);
  transition: transform var(--transition);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  display: flex;
  position: relative;
}

#chat-circle:hover {
  transform: scale(1.05);
}

#chat-circle:after {
  content: "";
  border: 2px solid #fff9;
  border-radius: 50%;
  width: 90%;
  height: 90%;
  animation: 1.5s infinite ripple;
  position: absolute;
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(.8);
  }

  100% {
    opacity: 0;
    transform: scale(1.5);
  }
}

#chatbot-container {
  background: var(--card-bg);
  width: 350px;
  -webkit-backdrop-filter: blur(var(--blur-medium));
  border-radius: var(--border-radius);
  box-shadow: 0 10px 25px var(--shadow);
  padding: 20px;
  display: none;
  position: fixed;
  bottom: 110px;
  right: 30px;
}

.chat-widget-open #chatbot-container {
  display: block;
}

#chatbot-header {
  background: var(--primary);
  color: var(--secondary);
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
  text-align: center;
  padding: 15px;
  font-size: 1.2rem;
}

#chat-history {
  -webkit-backdrop-filter: blur(3px);
  background: #ecf0f1cc;
  border-radius: 8px;
  height: 250px;
  margin-bottom: 15px;
  padding: 15px;
  overflow-y: auto;
}

#chat-input-area {
  gap: 10px;
  display: flex;
}

#chat-input {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  flex: 1;
  padding: 12px;
}

#send-btn, #chat-close-btn {
  background: var(--primary);
  color: var(--secondary);
  cursor: pointer;
  transition: background var(--transition);
  border: none;
  border-radius: 8px;
  padding: 12px 15px;
}

#send-btn:hover, #chat-close-btn:hover {
  background: var(--primary-light);
}

.modal {
  z-index: 1100;
  background: #0009;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  text-align: center;
  background: var(--card-bg);
  width: 90%;
  max-width: 500px;
  -webkit-backdrop-filter: blur(var(--blur-medium));
  border-radius: var(--border-radius);
  box-shadow: 0 10px 25px var(--shadow);
  margin: 10% auto;
  padding: 20px;
}

.modal-content p {
  color: #333;
  margin-bottom: 20px;
  font-size: 1rem;
}

.modal-content button {
  background: var(--primary);
  color: var(--secondary);
  cursor: pointer;
  transition: background var(--transition);
  border: none;
  border-radius: 8px;
  margin: 5px;
  padding: 12px 20px;
}

#confirmDelete {
  background: var(--accent-dark);
}

#cancelDelete {
  background: #95a5a6;
}

.close-btn {
  color: #aaa;
  float: right;
  cursor: pointer;
  transition: color var(--transition);
  font-size: 28px;
}

.close-btn:hover {
  color: #333;
}

.error-message {
  text-align: center;
  color: var(--accent-dark);
  margin-top: 10px;
  font-size: .9rem;
}

@media (width <= 768px) {
  .sign-in-container, #book-form-section, #book-list-section, .sidebar, #chatbot-container {
    width: 90%;
    padding: 20px;
  }

  .container {
    flex-direction: column;
    align-items: center;
  }
}
